import React, { useState, useRef, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { useLoadingOverlay } from "../context/LoadingOverlayContext";
import HeaderPage from "../components/layout/HeaderPage";
import { categories } from "../config/categoriesData";
import { FileText, Upload, X, CheckCircle } from "lucide-react";

interface QuoteFormData {
  name: string;
  email: string;
  phone: string;
  company: string;
  productType: string;
  quantity: string;
  description: string;
}

const QuotePage: React.FC = () => {
  const location = useLocation();
  const { show, hide } = useLoadingOverlay();
  const fileInputRef = useRef<HTMLInputElement>(null);

  // Get product info from URL parameters
  const urlParams = new URLSearchParams(location.search);
  const productFromUrl = urlParams.get('product') || '';
  const categoryFromUrl = urlParams.get('category') || '';
  const slugFromUrl = urlParams.get('slug') || '';

  // Find category name from slug
  const getCategoryName = (slug: string) => {
    const category = categories.find(cat => cat.slug === slug);
    return category?.name || slug;
  };



  const [formData, setFormData] = useState<QuoteFormData>({
    name: "",
    email: "",
    phone: "",
    company: "",
    productType: categoryFromUrl || location.state?.productType || "",
    quantity: "",
    description: productFromUrl ? `Tôi muốn báo giá cho sản phẩm: ${productFromUrl}` : "",
  });

  const [files, setFiles] = useState<File[]>([]);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formSubmitted, setFormSubmitted] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const urlParams = new URLSearchParams(location.search);
    const product = urlParams.get("product");
    const category = urlParams.get("category");

    if (product || category) {
      setFormData((prev) => ({
        ...prev,
        productType: category || prev.productType,
        description: product ? `Tôi muốn báo giá cho sản phẩm: ${product}` : prev.description
      }));
    }
  }, [location]);

  const handleInputChange = (
    e: React.ChangeEvent<
      HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement
    >,
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    if (error) setError(null);
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const newFiles = Array.from(e.target.files);
      setFiles((prev) => [...prev, ...newFiles]);
    }
  };

  const removeFile = (index: number) => {
    setFiles((prev) => prev.filter((_, i) => i !== index));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    show([]);

    try {
      // Create FormData for API call
      const formDataToSend = new FormData();

      // Add form fields
      formDataToSend.append("name", formData.name);
      formDataToSend.append("email", formData.email);
      formDataToSend.append("phone", formData.phone);
      formDataToSend.append("company", formData.company);
      formDataToSend.append("products", formData.productType);
      formDataToSend.append("quantity", formData.quantity);
      formDataToSend.append("message", formData.description);
      formDataToSend.append("subject", "Yêu cầu báo giá sản phẩm POSM");

      // Add files
      if (files && files.length > 0) {
        files.forEach((file) => {
          formDataToSend.append("attachments", file);
        });
      }

      console.log("Sending quote request to API...");
      const apiUrl = import.meta.env.VITE_API_URL || "https://api.kimtienposm.com";
      console.log("API URL:", apiUrl);
      const response = await fetch(`${apiUrl}/api/quote`, {
        method: "POST",
        body: formDataToSend,
      });

      const result = await response.json();
      console.log("API Response:", result);

      if (response.ok && result.success) {
        setFormSubmitted(true);
        setTimeout(() => {
          setFormSubmitted(false);
          setFormData({
            name: "",
            email: "",
            phone: "",
            company: "",
            productType: "",
            quantity: "",
            description: "",
          });
          setFiles([]);
        }, 5000);
      } else {
        throw new Error(result.message || "Có lỗi xảy ra khi gửi yêu cầu");
      }
    } catch (err: any) {
      console.error("Error submitting form:", err);
      setError(
        err.message || "Có lỗi xảy ra khi gửi yêu cầu. Vui lòng thử lại sau.",
      );
    } finally {
      setIsSubmitting(false);
      hide();
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <HeaderPage
        title="Nhận báo giá miễn phí"
        description="Chỉ cần 2 phút để điền form, chúng tôi sẽ phản hồi báo giá chi tiết trong vòng 24 giờ"
        breadcrumbs={[{ label: "Trang chủ", href: "/" }, { label: "Báo giá" }]}
        stats={[
          { value: "< 24h", label: "Phản hồi nhanh", color: "blue" },
          { value: "100%", label: "Miễn phí tư vấn", color: "green" },
          { value: "500+", label: "Dự án thành công", color: "purple" },
        ]}
        backgroundGradient="from-green-600 via-blue-600 to-purple-600"
      />

      {/* Main Form Section */}
      <div className="max-w-4xl mx-auto px-4 py-16 sm:px-6 lg:px-8">
        <div className="bg-white rounded-2xl shadow-xl p-8 border border-gray-100">
          <div className="mb-8 text-center">
            <h2 className="text-2xl font-bold text-gray-900 mb-2">
              Thông tin dự án
            </h2>
            <p className="text-gray-600">
              Vui lòng cung cấp thông tin chi tiết để chúng tôi báo giá chính
              xác nhất
            </p>
          </div>

          {formSubmitted ? (
            <div className="text-center py-12">
              <div className="w-16 h-16 bg-green-100 text-green-500 rounded-full flex items-center justify-center mx-auto mb-4">
                <CheckCircle className="w-8 h-8" />
              </div>
              <h3 className="text-2xl font-bold text-green-700 mb-2">
                Yêu cầu báo giá đã được gửi!
              </h3>
              <p className="text-gray-600 mb-4">
                Cảm ơn bạn đã gửi yêu cầu báo giá. Chúng tôi sẽ phản hồi trong
                vòng 24 giờ làm việc.
              </p>
              <div className="bg-green-50 border border-green-200 rounded-lg p-4 max-w-md mx-auto">
                <p className="text-green-700 text-sm">
                  <strong>Tiếp theo:</strong> Đội ngũ chuyên viên sẽ liên hệ với
                  bạn để tư vấn chi tiết và cung cấp báo giá tốt nhất.
                </p>
              </div>
            </div>
          ) : (
            <>
              {error && (
                <div className="mb-6 p-4 bg-red-50 text-red-600 rounded-lg border border-red-200 text-sm">
                  {error}
                </div>
              )}



              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Personal Information */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Họ và tên *
                    </label>
                    <input
                      type="text"
                      name="name"
                      value={formData.name}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                      placeholder="Nhập họ và tên của bạn"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Email *
                    </label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                      placeholder="email@example.com"
                    />
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Số điện thoại *
                    </label>
                    <input
                      type="tel"
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                      placeholder="0123 456 789"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Công ty/Tổ chức
                    </label>
                    <input
                      type="text"
                      name="company"
                      value={formData.company}
                      onChange={handleInputChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                      placeholder="Tên công ty (không bắt buộc)"
                    />
                  </div>
                </div>

                {/* Project Information */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Loại sản phẩm *
                    </label>
                    <select
                      name="productType"
                      value={formData.productType}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                    >
                      <option value="">Chọn loại sản phẩm</option>
                      {categories.map((category) => (
                        <option key={category.slug} value={category.slug}>
                          {category.name}
                        </option>
                      ))}
                      <option value="other">Khác</option>
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Số lượng dự kiến
                    </label>
                    <input
                      type="text"
                      name="quantity"
                      value={formData.quantity}
                      onChange={handleInputChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                      placeholder="VD: 1000 cái, 500 hộp..."
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Mô tả chi tiết dự án *
                  </label>
                  <textarea
                    name="description"
                    value={formData.description}
                    onChange={handleInputChange}
                    required
                    rows={4}
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200"
                    placeholder="Mô tả chi tiết về sản phẩm, kích thước, màu sắc, chất liệu..."
                  />
                </div>

                {/* File Upload */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Đính kèm file thiết kế (nếu có)
                  </label>
                  <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-blue-400 transition-colors duration-200">
                    <input
                      type="file"
                      ref={fileInputRef}
                      onChange={handleFileUpload}
                      multiple
                      accept=".pdf,.doc,.docx,.jpg,.jpeg,.png,.ai,.psd"
                      className="hidden"
                    />
                    <button
                      type="button"
                      onClick={() => fileInputRef.current?.click()}
                      className="text-blue-600 hover:text-blue-700 font-medium"
                    >
                      Chọn file để tải lên
                    </button>
                    <p className="text-sm text-gray-500 mt-2">
                      Hỗ trợ: PDF, DOC, JPG, PNG, AI, PSD (tối đa 10MB mỗi file)
                    </p>
                  </div>

                  {files.length > 0 && (
                    <div className="mt-4 space-y-2">
                      {files.map((file, index) => (
                        <div
                          key={index}
                          className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
                        >
                          <span className="text-sm text-gray-700">
                            {file.name}
                          </span>
                          <button
                            type="button"
                            onClick={() => removeFile(index)}
                            className="text-red-500 hover:text-red-700"
                          >
                            Xóa
                          </button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>

                {/* Submit Button */}
                <div className="text-center pt-6">
                  <button
                    type="submit"
                    disabled={isSubmitting}
                    className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 disabled:from-gray-400 disabled:to-gray-500 text-white font-bold py-4 px-8 rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-105 disabled:transform-none disabled:cursor-not-allowed"
                  >
                    {isSubmitting ? "Đang gửi..." : "Gửi yêu cầu báo giá"}
                  </button>
                </div>
              </form>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default QuotePage;
