import React from "react";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { Product } from "../config/productsData";
import { categories } from "../config/categoriesData";

interface ProductCardProps {
  product: Product;
  index: number;
  onQuote?: () => void;
}

// Product Card Component - Displays product information with category
const ProductCard: React.FC<ProductCardProps> = ({
  product,
  index,
  onQuote = () => {},
}) => {
  const navigate = useNavigate();

  // Get category display names
  const categoryNames = product.categoryslug.map(slug => {
    const categoryData = categories.find(cat => cat.slug === slug);
    return categoryData?.name || slug;
  });
  const [isImageLoaded, setIsImageLoaded] = React.useState(false);

  // Handle quote button click
  const handleQuoteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    // Navigate to quote page with product information pre-filled
    navigate(`/quote?product=${encodeURIComponent(product.name)}&category=${encodeURIComponent(product.categoryslug[0])}&slug=${encodeURIComponent(product.slug)}`);
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      viewport={{ once: true }}
      className="group bg-white rounded-xl border border-gray-100 hover:border-gray-200 transition-all duration-300 hover:shadow-lg"
    >
      {/* Image Container */}
      <div className="relative aspect-square bg-gray-50">
        {/* Category badges */}
        <div className="absolute bottom-3 left-3 right-3">
          <div className="flex flex-wrap gap-1">
            {categoryNames.slice(0, 2).map((name, index) => (
              <div key={index} className="text-xs font-medium text-gray-400 bg-white/80 backdrop-blur-sm px-2 py-1 rounded-md">
                {name}
              </div>
            ))}
            {categoryNames.length > 2 && (
              <div className="text-xs font-medium text-gray-400 bg-white/80 backdrop-blur-sm px-2 py-1 rounded-md">
                +{categoryNames.length - 2}
              </div>
            )}
          </div>
        </div>

        {/* Image với loading state và error handling */}
        <img
          src={product.pathimage}
          alt={product.name}
          className="w-full h-full object-contain p-6 transition-transform duration-300 group-hover:scale-105"
          onLoad={() => setIsImageLoaded(true)}
          onError={(e) => {
            // Fallback to a default image if the original fails to load
            e.currentTarget.src = "/product-images/standee/standee-1.svg";
            setIsImageLoaded(true);
          }}
          style={{ opacity: isImageLoaded ? 1 : 0, transition: "opacity 0.3s" }}
        />

        {/* Loading placeholder */}
        {!isImageLoaded && (
          <div className="absolute inset-0 bg-gray-200 animate-pulse flex items-center justify-center">
            <div className="text-gray-400 text-sm">Đang tải...</div>
          </div>
        )}
      </div>

      {/* Content - Đầy đủ thông tin */}
      <div className="p-5">
        {/* Title and Quote Button */}
        <div className="flex items-center justify-between gap-3">
          <h3 className="font-semibold text-gray-900 text-base leading-tight line-clamp-2 group-hover:text-blue-600 transition-colors duration-300 flex-1">
            {product.name}
          </h3>

          {/* Quote Button */}
          <button
            onClick={handleQuoteClick}
            className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white font-medium py-2 px-4 rounded-lg transition-all duration-300 shadow-sm hover:shadow-md transform hover:scale-105 text-sm whitespace-nowrap"
          >
            Báo giá
          </button>
        </div>

        {/* Brand info */}
        <div className="mt-2">
          <span className="text-xs text-gray-500">{product.brands}</span>
        </div>
      </div>
    </motion.div>
  );
};

export default ProductCard;
