import React, { useState } from "react";
import { Search, X, Grid3X3 } from "lucide-react";
import { categories } from "../config/categoriesData";
import { getProductsByCategory } from "../utils/productUtils";

interface CategorySidebarProps {
  selectedCategory: string;
  searchQuery: string;
  onCategoryChange: (category: string) => void;
  onSearchChange: (query: string) => void;
  totalProducts: number;
}

const CategorySidebar: React.FC<CategorySidebarProps> = ({
  selectedCategory,
  searchQuery,
  onCategoryChange,
  onSearchChange,
  totalProducts,
}) => {
  const [isSearchFocused, setIsSearchFocused] = useState(false);

  // Get categories with product counts
  const categoriesWithCounts = categories.map((category) => ({
    ...category,
    productCount: getProductsByCategory(category.slug).length,
  }));

  // Filter out categories with no products for cleaner display
  const availableCategories = categoriesWithCounts.filter(
    (category) => category.productCount > 0
  );

  return (
    <div className="bg-white/80 backdrop-blur-sm rounded-2xl border border-slate-200/60 shadow-sm sticky top-28 overflow-hidden">
      <div className="p-7">
        {/* Elegant Header */}
        <div className="mb-7">
          <div className="flex items-center gap-2 mb-2">
            <Grid3X3 className="w-4 h-4 text-slate-400" />
            <h3 className="text-base font-medium text-slate-800 tracking-tight">
              Danh mục sản phẩm
            </h3>
          </div>
          <p className="text-xs text-slate-500 font-light">
            {availableCategories.length} danh mục • {totalProducts} sản phẩm
          </p>
        </div>

        {/* Refined Search */}
        <div className="mb-7">
          <div className="relative group">
            <Search className={`w-4 h-4 absolute left-3.5 top-1/2 transform -translate-y-1/2 transition-colors duration-200 ${
              isSearchFocused ? "text-indigo-400" : "text-slate-400"
            }`} />
            <input
              type="text"
              placeholder="Tìm kiếm..."
              value={searchQuery}
              onChange={(e) => onSearchChange(e.target.value)}
              onFocus={() => setIsSearchFocused(true)}
              onBlur={() => setIsSearchFocused(false)}
              className={`w-full pl-11 pr-10 py-3 bg-slate-50/50 border border-slate-200/60 rounded-xl
                focus:bg-white focus:border-indigo-200 focus:ring-2 focus:ring-indigo-100
                transition-all duration-200 text-sm placeholder-slate-400 font-light
                hover:bg-slate-50 hover:border-slate-300/60`}
            />
            {searchQuery && (
              <button
                onClick={() => onSearchChange("")}
                className="absolute right-3 top-1/2 transform -translate-y-1/2 text-slate-400 hover:text-slate-600
                  transition-all duration-200 hover:bg-slate-100 rounded-full p-1"
              >
                <X className="w-3.5 h-3.5" />
              </button>
            )}
          </div>
          {searchQuery && (
            <div className="mt-2 text-xs text-slate-500 font-light">
              Đang tìm kiếm "{searchQuery}"
            </div>
          )}
        </div>

        {/* Elegant Category Navigation */}
        <nav className="space-y-1.5">
          {/* All Products */}
          <button
            onClick={() => onCategoryChange("all")}
            className={`group w-full text-left px-4 py-3.5 rounded-xl transition-all duration-200 text-sm font-medium
              ${selectedCategory === "all"
                ? "bg-gradient-to-r from-indigo-500 to-purple-500 text-white shadow-lg shadow-indigo-200/50 scale-[1.02]"
                : "text-slate-700 hover:bg-slate-50/80 hover:text-slate-900 hover:scale-[1.01]"
              }`}
          >
            <div className="flex items-center justify-between">
              <span className="font-medium">Tất cả sản phẩm</span>
              <span className={`text-xs px-2 py-1 rounded-full font-medium transition-colors duration-200 ${
                selectedCategory === "all"
                  ? "bg-white/20 text-white"
                  : "bg-slate-100 text-slate-500 group-hover:bg-slate-200"
              }`}>
                {totalProducts}
              </span>
            </div>
          </button>

          {/* Category List */}
          {availableCategories.map((category, index) => {
            const isActive = selectedCategory === category.slug;

            return (
              <button
                key={category.slug}
                onClick={() => onCategoryChange(category.slug)}
                style={{ animationDelay: `${index * 50}ms` }}
                className={`group w-full text-left px-4 py-3.5 rounded-xl transition-all duration-200 text-sm font-medium
                  animate-in fade-in slide-in-from-left-2
                  ${isActive
                    ? "bg-gradient-to-r from-indigo-500 to-purple-500 text-white shadow-lg shadow-indigo-200/50 scale-[1.02]"
                    : "text-slate-700 hover:bg-slate-50/80 hover:text-slate-900 hover:scale-[1.01]"
                  }`}
              >
                <div className="flex items-center justify-between">
                  <span className="font-medium">{category.name}</span>
                  <span className={`text-xs px-2 py-1 rounded-full font-medium transition-colors duration-200 ${
                    isActive
                      ? "bg-white/20 text-white"
                      : "bg-slate-100 text-slate-500 group-hover:bg-slate-200"
                  }`}>
                    {category.productCount}
                  </span>
                </div>
              </button>
            );
          })}
        </nav>

        {/* Subtle Footer */}
        {!searchQuery && availableCategories.length > 0 && (
          <div className="mt-6 pt-5 border-t border-slate-100/80">
            <div className="text-center">
              <p className="text-xs text-slate-400 font-light">
                Chọn danh mục để lọc sản phẩm
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default CategorySidebar;
