import React, { useState } from "react";
import {
  Menu,
  X,
  ArrowRight,
  ChevronDown,
} from "lucide-react";
import { Link, useLocation } from "react-router-dom";
import { motion, AnimatePresence } from "framer-motion";
import { categories } from "../../config/categoriesData";
import { LOGOS } from "../../config/logos";

const Header: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isProductDropdownOpen, setIsProductDropdownOpen] = useState(false);
  const [isAboutDropdownOpen, setIsAboutDropdownOpen] = useState(false);
  const location = useLocation();



  // Get category URL - using new category slugs
  const getCategoryUrl = (categorySlug: string): string => {
    return `/products?category=${categorySlug}`;
  };

  // Close mobile menu when clicking navigation links
  const handleNavClick = () => {
    setIsMenuOpen(false);
  };

  return (
    <motion.header
      initial={{ opacity: 0, y: -40 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.7, ease: "easeOut" }}
      className="bg-white shadow-lg fixed w-full top-0 z-50 min-h-[80px]"
    >
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center py-4">
          {/* Logo */}
          <motion.div
            whileHover={{ scale: 1.08 }}
            transition={{ type: "spring", stiffness: 300, damping: 18 }}
          >
            <Link to="/" className="flex items-center space-x-3">
              <motion.img
                src={LOGOS.main.src}
                alt={LOGOS.main.alt}
                className="h-12 w-auto"
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.6, delay: 0.1, ease: "easeOut" }}
              />
              <div className="flex flex-col">
                <span className="text-2xl font-bold text-blue-700">
                  Kim Tiền
                </span>
                <span className="text-xs text-red-500 font-medium">
                  Kết nối thành công - Vươn tầm cao mới
                </span>
              </div>
            </Link>
          </motion.div>

          {/* Desktop Navigation */}
          <motion.nav
            className="hidden md:flex items-center space-x-8"
            initial="hidden"
            animate="visible"
            variants={{
              hidden: {},
              visible: { transition: { staggerChildren: 0.08 } },
            }}
          >
            {/* Trang chủ */}
            <motion.div
              variants={{
                hidden: { opacity: 0, y: 20 },
                visible: {
                  opacity: 1,
                  y: 0,
                  transition: { duration: 0.5, ease: "easeOut" },
                },
              }}
              whileHover={{ scale: 1.05 }}
              style={{ display: "inline-block" }}
              className="relative group"
            >
              <Link
                to="/"
                className={`text-gray-700 hover:text-blue-700 font-medium relative px-3 py-2 transition-all duration-300 ${location.pathname === "/" ? "text-blue-700" : ""}`}
              >
                Trang chủ
                {/* Active state underline */}
                <motion.span
                  className="absolute bottom-0 left-0 h-0.5 bg-blue-700 rounded-full"
                  animate={{
                    width: location.pathname === "/" ? "100%" : "0%",
                    opacity: location.pathname === "/" ? 1 : 0,
                  }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                  style={{ display: "block" }}
                />
                {/* Hover effect underline */}
                <motion.span
                  className="absolute bottom-0 left-0 h-0.5 bg-blue-400 rounded-full opacity-0 group-hover:opacity-100"
                  initial={{ width: "0%" }}
                  whileHover={{ width: "100%" }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                  style={{
                    display: location.pathname === "/" ? "none" : "block",
                  }}
                />
              </Link>
            </motion.div>

            {/* Products Dropdown - Simple & Clean */}
            <motion.div
              className="relative"
              variants={{
                hidden: { opacity: 0, y: 20 },
                visible: {
                  opacity: 1,
                  y: 0,
                  transition: { duration: 0.5, ease: "easeOut" },
                },
              }}
              onMouseEnter={() => setIsProductDropdownOpen(true)}
              onMouseLeave={() => setIsProductDropdownOpen(false)}
            >
              <button
                className={`text-gray-700 hover:text-blue-700 font-medium relative group flex items-center gap-2 px-3 py-2 transition-all duration-300 ${
                  location.pathname === "/products" ? "text-blue-700" : ""
                }`}
              >
                <span>Sản phẩm</span>
                <ChevronDown
                  className={`w-4 h-4 transition-transform duration-300 ${isProductDropdownOpen ? "rotate-180" : ""}`}
                />
                {/* Active state underline */}
                <motion.span
                  className="absolute bottom-0 left-0 h-0.5 bg-blue-700 rounded-full"
                  animate={{
                    width: location.pathname === "/products" ? "100%" : "0%",
                    opacity: location.pathname === "/products" ? 1 : 0,
                  }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                  style={{ display: "block" }}
                />
                {/* Hover/dropdown effect underline */}
                <motion.span
                  className="absolute bottom-0 left-0 h-0.5 bg-blue-400 rounded-full"
                  animate={{
                    width:
                      isProductDropdownOpen && location.pathname !== "/san-pham"
                        ? "100%"
                        : "0%",
                    opacity:
                      isProductDropdownOpen && location.pathname !== "/san-pham"
                        ? 1
                        : 0,
                  }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                  style={{ display: "block" }}
                />
              </button>

              {/* Simple Dropdown Menu */}
              <AnimatePresence>
                {isProductDropdownOpen && (
                  <motion.div
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: 10 }}
                    transition={{ duration: 0.2 }}
                    className="absolute top-full left-1/2 transform -translate-x-1/2 bg-white rounded-xl shadow-xl border border-gray-200 py-3 z-50 min-w-[400px]"
                  >
                    {/* All Products Link */}
                    <Link
                      to="/products"
                      className="block px-4 py-3 text-sm font-semibold text-blue-600 hover:text-blue-700 hover:bg-blue-50 transition-colors border-b border-gray-100 mb-2 rounded-t-xl"
                    >
                      <div className="flex items-center">
                        <div className="w-6 h-6 rounded-md bg-gradient-to-br from-blue-500 to-blue-600 mr-3"></div>
                        Xem tất cả sản phẩm
                      </div>
                    </Link>

                    {/* Categories List */}
                    <div className="space-y-1">
                      {categories.map((category) => (
                        <Link
                          key={category.slug}
                          to={getCategoryUrl(category.slug)}
                          className="flex items-center px-4 py-2.5 hover:bg-gray-50 transition-colors group rounded-lg mx-2"
                        >
                          <div
                            className={`w-8 h-8 rounded-lg bg-gradient-to-br from-blue-500 to-blue-600 mr-3 shadow-sm group-hover:shadow-md transition-all flex items-center justify-center`}
                          >
                            <span className="text-white text-sm font-bold">
                              {category.name.charAt(0)}
                            </span>
                          </div>
                          <div className="flex-1">
                            <div className="font-medium text-gray-900 group-hover:text-blue-700 transition-colors">
                              {category.name}
                            </div>
                          </div>
                          <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 group-hover:translate-x-1 transition-all" />
                        </Link>
                      ))}
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>

            {/* About Dropdown */}
            <motion.div
              className="relative"
              variants={{
                hidden: { opacity: 0, y: 20 },
                visible: {
                  opacity: 1,
                  y: 0,
                  transition: { duration: 0.5, ease: "easeOut" },
                },
              }}
              onMouseEnter={() => setIsAboutDropdownOpen(true)}
              onMouseLeave={() => setIsAboutDropdownOpen(false)}
            >
              <button
                className={`text-gray-700 hover:text-blue-700 font-medium relative group flex items-center gap-2 px-3 py-2 transition-all duration-300 ${
                  location.pathname.startsWith("/company") ||
                  location.pathname.startsWith("/blog") ||
                  location.pathname.startsWith("/faq") ||
                  location.pathname.startsWith("/contact") ? "text-blue-700" : ""
                }`}
              >
                <span>Về chúng tôi</span>
                <ChevronDown
                  className={`w-4 h-4 transition-transform duration-300 ${isAboutDropdownOpen ? "rotate-180" : ""}`}
                />
                {/* Active state underline */}
                <motion.span
                  className="absolute bottom-0 left-0 h-0.5 bg-blue-700 rounded-full"
                  animate={{
                    width: (location.pathname.startsWith("/company") ||
                           location.pathname.startsWith("/blog") ||
                           location.pathname.startsWith("/faq") ||
                           location.pathname.startsWith("/contact")) ? "100%" : "0%",
                    opacity: (location.pathname.startsWith("/company") ||
                             location.pathname.startsWith("/blog") ||
                             location.pathname.startsWith("/faq") ||
                             location.pathname.startsWith("/contact")) ? 1 : 0,
                  }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                  style={{ display: "block" }}
                />
                {/* Hover/dropdown effect underline */}
                <motion.span
                  className="absolute bottom-0 left-0 h-0.5 bg-blue-400 rounded-full"
                  animate={{
                    width: isAboutDropdownOpen && !(location.pathname.startsWith("/company") ||
                           location.pathname.startsWith("/blog") ||
                           location.pathname.startsWith("/faq") ||
                           location.pathname.startsWith("/contact")) ? "100%" : "0%",
                    opacity: isAboutDropdownOpen && !(location.pathname.startsWith("/company") ||
                            location.pathname.startsWith("/blog") ||
                            location.pathname.startsWith("/faq") ||
                            location.pathname.startsWith("/contact")) ? 1 : 0,
                  }}
                  transition={{ duration: 0.3, ease: "easeInOut" }}
                  style={{ display: "block" }}
                />
              </button>

              {/* About Dropdown Menu */}
              <AnimatePresence>
                {isAboutDropdownOpen && (
                  <motion.div
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: 10 }}
                    transition={{ duration: 0.2 }}
                    className="absolute top-full left-1/2 transform -translate-x-1/2 bg-white rounded-xl shadow-xl border border-gray-200 py-3 z-50 min-w-[250px]"
                  >
                    <div className="space-y-1">
                      <Link
                        to="/company"
                        className="flex items-center px-4 py-2.5 hover:bg-gray-50 transition-colors group rounded-lg mx-2"
                      >
                        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-blue-500 to-blue-600 mr-3 shadow-sm group-hover:shadow-md transition-all flex items-center justify-center">
                          <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="font-medium text-gray-900 group-hover:text-blue-700 transition-colors">
                            Công ty
                          </div>
                        </div>
                        <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 group-hover:translate-x-1 transition-all" />
                      </Link>

                      <Link
                        to="/blog"
                        className="flex items-center px-4 py-2.5 hover:bg-gray-50 transition-colors group rounded-lg mx-2"
                      >
                        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-purple-500 to-purple-600 mr-3 shadow-sm group-hover:shadow-md transition-all flex items-center justify-center">
                          <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 20H5a2 2 0 01-2-2V6a2 2 0 012-2h10a2 2 0 012 2v1m2 13a2 2 0 01-2-2V7m2 13a2 2 0 002-2V9a2 2 0 00-2-2h-2m-4-3H9M7 16h6M7 8h6v4H7V8z" />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="font-medium text-gray-900 group-hover:text-blue-700 transition-colors">
                            Blog
                          </div>
                        </div>
                        <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 group-hover:translate-x-1 transition-all" />
                      </Link>

                      <Link
                        to="/faq"
                        className="flex items-center px-4 py-2.5 hover:bg-gray-50 transition-colors group rounded-lg mx-2"
                      >
                        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-green-500 to-green-600 mr-3 shadow-sm group-hover:shadow-md transition-all flex items-center justify-center">
                          <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8.228 9c.549-1.165 2.03-2 3.772-2 2.21 0 4 1.343 4 3 0 1.4-1.278 2.575-3.006 2.907-.542.104-.994.54-.994 1.093m0 3h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="font-medium text-gray-900 group-hover:text-blue-700 transition-colors">
                            FAQ
                          </div>
                        </div>
                        <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 group-hover:translate-x-1 transition-all" />
                      </Link>

                      <Link
                        to="/contact"
                        className="flex items-center px-4 py-2.5 hover:bg-gray-50 transition-colors group rounded-lg mx-2"
                      >
                        <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-orange-500 to-orange-600 mr-3 shadow-sm group-hover:shadow-md transition-all flex items-center justify-center">
                          <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M3 8l7.89 4.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                          </svg>
                        </div>
                        <div className="flex-1">
                          <div className="font-medium text-gray-900 group-hover:text-blue-700 transition-colors">
                            Liên hệ
                          </div>
                        </div>
                        <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 group-hover:translate-x-1 transition-all" />
                      </Link>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>


          </motion.nav>

          {/* Desktop CTA */}
          <motion.div
            className="hidden md:flex items-center"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.4, ease: "easeOut" }}
            whileHover={{ scale: 1.06 }}
          >
            <Link
              to="/quote"
              className="bg-blue-600 text-white px-5 py-2 rounded-lg font-medium shadow-md"
            >
              Báo giá
            </Link>
          </motion.div>

          {/* Mobile menu button */}
          <div className="md:hidden">
            <motion.button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="text-gray-700"
              aria-label={isMenuOpen ? "Close menu" : "Open menu"}
              whileTap={{ scale: 0.92 }}
            >
              {isMenuOpen ? (
                <X className="h-6 w-6" />
              ) : (
                <Menu className="h-6 w-6" />
              )}
            </motion.button>
          </div>
        </div>

        {/* Mobile Navigation */}
        <AnimatePresence>
          {isMenuOpen && (
            <motion.div
              className="md:hidden pb-6 pt-2 border-t border-gray-100"
              initial={{ opacity: 0, y: -30 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -30 }}
              transition={{ duration: 0.4, ease: "easeOut" }}
            >
              <nav className="flex flex-col space-y-4">
                {/* Regular nav items */}
                <Link
                  to="/"
                  className={`text-gray-700 hover:text-blue-700 font-medium px-2 py-2 rounded-md ${location.pathname === "/" ? "text-blue-700 bg-blue-50" : ""}`}
                  onClick={handleNavClick}
                >
                  Trang chủ
                </Link>

                {/* Products with categories */}
                <div className="space-y-2">
                  <Link
                    to="/products"
                    className={`text-gray-700 hover:text-blue-700 font-medium px-2 py-2 rounded-md block ${location.pathname === "/products" ? "text-blue-700 bg-blue-50" : ""}`}
                    onClick={handleNavClick}
                  >
                    Sản phẩm
                  </Link>
                  <div className="ml-4 space-y-1">
                    {categories.map((category) => (
                      <Link
                        key={category.slug}
                        to={getCategoryUrl(category.slug)}
                        className="flex items-center px-2 py-1 text-sm text-gray-600 hover:text-blue-600 rounded-md"
                        onClick={handleNavClick}
                      >
                        <div
                          className={`mr-2 w-4 h-4 rounded bg-gradient-to-br from-blue-500 to-blue-600`}
                        ></div>
                        {category.name}
                      </Link>
                    ))}
                  </div>
                </div>

                {/* About section with sub-items */}
                <div className="space-y-2">
                  <div className="text-gray-700 font-medium px-2 py-2">
                    Về chúng tôi
                  </div>
                  <div className="ml-4 space-y-1">
                    <Link
                      to="/company"
                      className={`flex items-center px-2 py-1 text-sm text-gray-600 hover:text-blue-600 rounded-md ${location.pathname === "/company" ? "text-blue-600 bg-blue-50" : ""}`}
                      onClick={handleNavClick}
                    >
                      <div className="mr-2 w-4 h-4 rounded bg-gradient-to-br from-blue-500 to-blue-600"></div>
                      Công ty
                    </Link>
                    <Link
                      to="/blog"
                      className={`flex items-center px-2 py-1 text-sm text-gray-600 hover:text-blue-600 rounded-md ${location.pathname.startsWith("/blog") ? "text-blue-600 bg-blue-50" : ""}`}
                      onClick={handleNavClick}
                    >
                      <div className="mr-2 w-4 h-4 rounded bg-gradient-to-br from-purple-500 to-purple-600"></div>
                      Blog
                    </Link>
                    <Link
                      to="/faq"
                      className={`flex items-center px-2 py-1 text-sm text-gray-600 hover:text-blue-600 rounded-md ${location.pathname === "/faq" ? "text-blue-600 bg-blue-50" : ""}`}
                      onClick={handleNavClick}
                    >
                      <div className="mr-2 w-4 h-4 rounded bg-gradient-to-br from-green-500 to-green-600"></div>
                      FAQ
                    </Link>
                    <Link
                      to="/contact"
                      className={`flex items-center px-2 py-1 text-sm text-gray-600 hover:text-blue-600 rounded-md ${location.pathname === "/contact" ? "text-blue-600 bg-blue-50" : ""}`}
                      onClick={handleNavClick}
                    >
                      <div className="mr-2 w-4 h-4 rounded bg-gradient-to-br from-orange-500 to-orange-600"></div>
                      Liên hệ
                    </Link>
                  </div>
                </div>

                {/* Mobile CTA */}
                <div className="pt-2 grid grid-cols-2 gap-3">
                  <Link
                    to="/quote"
                    className="bg-blue-600 text-white px-4 py-2 rounded-lg font-medium w-full shadow-md"
                    onClick={handleNavClick}
                  >
                    Báo giá
                  </Link>
                </div>
              </nav>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </motion.header>
  );
};

export default Header;
