import { productsData, Product } from "../config/productsData";
import { categories } from "../config/categoriesData";

// Get products by category slug
export const getProductsByCategory = (categorySlug: string): Product[] => {
  if (categorySlug === "all") return productsData;
  return productsData.filter(product => product.categoryslug.includes(categorySlug));
};

// Get category display name by slug
export const getCategoryDisplayName = (categorySlug: string): string => {
  const category = categories.find(cat => cat.slug === categorySlug);
  return category?.name || categorySlug;
};

// Get all unique category slugs from products
export const getAvailableCategories = (): string[] => {
  const categorySet = new Set(productsData.flatMap(product => product.categoryslug));
  return Array.from(categorySet);
};

// Get category statistics
export const getCategoryStats = () => {
  return categories.map(category => ({
    ...category,
    productCount: getProductsByCategory(category.slug).length
  }));
};

// Search products by name, category, or brand
export const searchProducts = (query: string): Product[] => {
  if (!query.trim()) return productsData;

  const searchTerm = query.toLowerCase();
  return productsData.filter(product =>
    product.name.toLowerCase().includes(searchTerm) ||
    product.categoryslug.some(cat => cat.toLowerCase().includes(searchTerm)) ||
    product.brands.toLowerCase().includes(searchTerm) ||
    product.categoryslug.some(cat => getCategoryDisplayName(cat).toLowerCase().includes(searchTerm))
  );
};

// Sort products
export const sortProducts = (products: Product[], sortBy: string): Product[] => {
  const sorted = [...products];

  switch (sortBy) {
    case "name":
      return sorted.sort((a, b) => a.name.localeCompare(b.name));
    case "newest":
      return sorted.reverse(); // Assuming newer items are at the end
    case "category":
      return sorted.sort((a, b) => a.categoryslug[0].localeCompare(b.categoryslug[0]));
    default:
      return sorted;
  }
};

// Get products with filtering and sorting
export const getFilteredProducts = (
  categorySlug: string = "all",
  searchQuery: string = "",
  sortBy: string = "newest"
): Product[] => {
  let filtered = getProductsByCategory(categorySlug);

  if (searchQuery.trim()) {
    const searchTerm = searchQuery.toLowerCase();
    filtered = filtered.filter(product =>
      product.name.toLowerCase().includes(searchTerm) ||
      product.categoryslug.some(cat => cat.toLowerCase().includes(searchTerm)) ||
      product.brands.toLowerCase().includes(searchTerm) ||
      product.categoryslug.some(cat => getCategoryDisplayName(cat).toLowerCase().includes(searchTerm))
    );
  }

  return sortProducts(filtered, sortBy);
};
