import React, { createContext, useContext, useState } from "react";

interface LoadingOverlayContextType {
  show: (images: string[]) => void;
  hide: () => void;
  visible: boolean;
}

const LoadingOverlayContext = createContext<LoadingOverlayContextType>({
  show: () => {},
  hide: () => {},
  visible: false,
});

export const useLoadingOverlay = () => useContext(LoadingOverlayContext);

export const LoadingOverlayProvider: React.FC<{
  children: React.ReactNode;
}> = ({ children }) => {
  const [visible, setVisible] = useState(false);

  const show = (images: string[]) => {
    setVisible(true);
    Promise.all(
      images.map((src) => {
        return new Promise((resolve) => {
          const img = new window.Image();
          img.onload = img.onerror = resolve;
          img.src = src;
        });
      }),
    ).then(() => setTimeout(() => setVisible(false), 500));
  };

  const hide = () => setVisible(false);

  return (
    <LoadingOverlayContext.Provider value={{ show, hide, visible }}>
      {children}
      {visible && (
        <div className="fixed inset-0 z-[9999] flex items-center justify-center bg-gradient-to-br from-slate-900 via-blue-900 to-indigo-900 transition-all duration-700 opacity-100">
          {/* Modern Glass Morphism Background */}
          <div className="absolute inset-0 overflow-hidden">
            {/* Animated Glass Orbs */}
            <div className="absolute top-20 left-20 w-80 h-80 bg-gradient-to-r from-blue-400/20 to-purple-400/20 rounded-full blur-3xl animate-pulse"></div>
            <div className="absolute bottom-20 right-20 w-96 h-96 bg-gradient-to-r from-purple-400/20 to-pink-400/20 rounded-full blur-3xl animate-pulse delay-1000"></div>
            <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-64 h-64 bg-gradient-to-r from-indigo-400/20 to-blue-400/20 rounded-full blur-3xl animate-pulse delay-500"></div>

            {/* Floating Particles */}
            <div className="absolute top-1/4 left-1/4 w-3 h-3 bg-white/30 rounded-full animate-float opacity-60"></div>
            <div className="absolute top-1/3 right-1/4 w-2 h-2 bg-blue-300/40 rounded-full animate-bounce opacity-70"></div>
            <div className="absolute bottom-1/4 left-1/3 w-4 h-4 bg-purple-300/30 rounded-full animate-pulse opacity-50"></div>
            <div className="absolute top-1/2 right-1/3 w-2 h-2 bg-pink-300/40 rounded-full animate-ping opacity-60"></div>
            <div className="absolute bottom-1/3 right-1/5 w-3 h-3 bg-indigo-300/30 rounded-full animate-bounce opacity-80"></div>

            {/* Glass Overlay */}
            <div className="absolute inset-0 bg-white/5 backdrop-blur-sm"></div>
          </div>

          {/* Main Loading Content */}
          <div className="relative flex flex-col items-center z-10">
            {/* Modern Glass Logo */}
            <div className="mb-12 relative">
              <div className="w-24 h-24 bg-white/10 backdrop-blur-md rounded-3xl flex items-center justify-center shadow-2xl border border-white/20 transform hover:scale-105 transition-all duration-500">
                <svg
                  className="w-12 h-12 text-white"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M19.428 15.428a2 2 0 00-1.022-.547l-2.387-.477a6 6 0 00-3.86.517l-.318.158a6 6 0 01-3.86.517L6.05 15.21a2 2 0 00-1.806.547M8 4h8l-1 1v5.172a2 2 0 00.586 1.414l5 5c1.26 1.26.367 3.414-1.415 3.414H4.828c-1.782 0-2.674-2.154-1.414-3.414l5-5A2 2 0 009 10.172V5L8 4z"
                  />
                </svg>
              </div>
              <div className="absolute -inset-4 bg-gradient-to-r from-blue-500/20 via-purple-500/20 to-pink-500/20 rounded-3xl blur-xl animate-pulse"></div>
            </div>

            {/* Glass Morphism Spinner */}
            <div className="relative mb-12">
              {/* Outer Glass Ring */}
              <div className="w-32 h-32 border-4 border-transparent border-t-white/40 border-r-blue-300/60 rounded-full animate-spin backdrop-blur-sm"></div>
              {/* Inner Glass Ring */}
              <div className="absolute top-3 left-3 w-26 h-26 border-4 border-transparent border-b-purple-300/50 border-l-pink-300/50 rounded-full animate-spin-reverse backdrop-blur-sm"></div>
              {/* Center Glass Dot */}
              <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-6 h-6 bg-white/20 backdrop-blur-md rounded-full animate-pulse border border-white/30 shadow-lg"></div>
              {/* Glow Effect */}
              <div className="absolute inset-0 w-32 h-32 border-4 border-white/10 rounded-full animate-ping"></div>
            </div>

            {/* Modern Loading Text */}
            <div className="text-center">
              <h2 className="text-3xl font-black bg-gradient-to-r from-white via-blue-100 to-purple-100 bg-clip-text text-transparent mb-4 drop-shadow-lg">
                Kim Tien POSM
              </h2>

              <div className="bg-white/10 backdrop-blur-md rounded-2xl p-6 mb-6 border border-white/20 shadow-lg">
                <div className="text-white/90 text-xl font-medium flex items-center justify-center gap-3 mb-4">
                  <span className="loading-text">Đang tải nội dung</span>
                  <div className="flex space-x-1">
                    <div className="w-2 h-2 bg-white/60 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-white/60 rounded-full animate-bounce delay-100"></div>
                    <div className="w-2 h-2 bg-white/60 rounded-full animate-bounce delay-200"></div>
                  </div>
                </div>

                {/* Glass Progress Bar */}
                <div className="w-80 h-3 bg-white/10 backdrop-blur-sm rounded-full overflow-hidden border border-white/20">
                  <div className="h-full bg-gradient-to-r from-blue-400 via-purple-400 to-pink-400 rounded-full animate-loading-bar shadow-sm"></div>
                </div>
              </div>

              <p className="text-white/70 text-lg font-light">
                Chuẩn bị trải nghiệm tuyệt vời...
              </p>
            </div>

            {/* Floating Icons */}
            <div className="absolute -top-10 -left-10 text-blue-400 opacity-60 animate-float">
              <svg
                className="w-6 h-6"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M9 12l2 2l4-4"
                />
              </svg>
            </div>

            <div className="absolute -top-5 -right-15 text-purple-400 opacity-60 animate-float animation-delay-1000">
              <svg
                className="w-5 h-5"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M13 10V3L4 14h7v7l9-11h-7z"
                />
              </svg>
            </div>

            <div className="absolute -bottom-8 -right-8 text-pink-400 opacity-60 animate-float animation-delay-2000">
              <svg
                className="w-4 h-4"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M5 3v4M3 5h4M6 17v4m-2-2h4m5-16l2.286 6.857L21 12l-5.714 2.143L13 21l-2.286-6.857L5 12l5.714-2.143L13 3z"
                />
              </svg>
            </div>
          </div>

          {/* Custom Styles */}
          <style>{`
            .loading-dots::after {
              content: '';
              display: inline-block;
              width: 1.2em;
              text-align: left;
              animation: dots 1.5s steps(3, end) infinite;
            }

            @keyframes dots {
              0%, 20% { content: ''; }
              40% { content: '.'; }
              60% { content: '..'; }
              80%, 100% { content: '...'; }
            }

            @keyframes blob {
              0% { transform: translate(0px, 0px) scale(1); }
              33% { transform: translate(30px, -50px) scale(1.1); }
              66% { transform: translate(-20px, 20px) scale(0.9); }
              100% { transform: translate(0px, 0px) scale(1); }
            }

            @keyframes loading-bar {
              0% { width: 0%; }
              50% { width: 70%; }
              100% { width: 100%; }
            }

            @keyframes float {
              0%, 100% { transform: translateY(0px); }
              50% { transform: translateY(-20px); }
            }

            @keyframes spin-reverse {
              from { transform: rotate(360deg); }
              to { transform: rotate(0deg); }
            }

            .animate-blob { animation: blob 7s infinite; }
            .animate-loading-bar { animation: loading-bar 3s ease-in-out infinite; }
            .animate-float { animation: float 3s ease-in-out infinite; }
            .animate-spin-reverse { animation: spin-reverse 2s linear infinite; }
            .animation-delay-1000 { animation-delay: 1s; }
            .animation-delay-2000 { animation-delay: 2s; }
            .animation-delay-4000 { animation-delay: 4s; }
          `}</style>
        </div>
      )}
    </LoadingOverlayContext.Provider>
  );
};
