import React, { useState } from "react";
import { useScrollAnimation } from "../hooks/useScrollAnimation";
import { motion } from "framer-motion";
import {
  Mail,
  Phone,
  MessageCircle,
  User,
  Users,
  Zap,
  Facebook,
} from "lucide-react";
import toast from "react-hot-toast";
import HeaderPage from "../components/layout/HeaderPage";
import { staffData, companyContact, contactSubjects } from "../config/contactData";

// Local validation function
const validateEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};



const ContactPage: React.FC = () => {
  useScrollAnimation();
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    subject: "",
    message: "",
  });
  const [formSubmitted, setFormSubmitted] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleInputChange = (
    e: React.ChangeEvent<
      HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement
    >,
  ) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    if (error) setError(null);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateEmail(formData.email)) {
      setError("Vui lòng nhập địa chỉ email hợp lệ");
      return;
    }

    setIsSubmitting(true);
    setError(null);

    try {
      // Simulate form submission for standalone frontend
      console.log('Contact Form Data:', formData);

      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 1500));

      // Simulate success
      setFormSubmitted(true);
      toast.success("✅ Tin nhắn đã được gửi thành công!");

      setTimeout(() => {
        setFormSubmitted(false);
        setFormData({
          name: "",
          email: "",
          phone: "",
          subject: "",
          message: "",
        });
      }, 5000);
    } catch (err: any) {
      console.error("Contact form error:", err);
      setError(
        err.message || "Đã xảy ra lỗi khi gửi email. Vui lòng thử lại sau.",
      );
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="bg-gray-50 min-h-screen">
      {/* Modern Header */}
      <HeaderPage
        title="Liên hệ"
        subtitle="với chúng tôi"
        description="Chúng tôi luôn sẵn sàng lắng nghe và hỗ trợ bạn. Hãy liên hệ với chúng tôi để được tư vấn miễn phí về các giải pháp in ấn và POSM."
        breadcrumbs={[{ label: "Trang chủ", href: "/" }, { label: "Liên hệ" }]}
        stats={[
          { value: "24/7", label: "Hỗ trợ", color: "blue" },
          { value: "< 1h", label: "Phản hồi", color: "green" },
          { value: "100%", label: "Tư vấn miễn phí", color: "purple" },
        ]}
      />

      {/* Staff grid */}
      <section className="py-16 px-4">
        <motion.div
          className="max-w-6xl mx-auto grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8"
          initial="hidden"
          animate="visible"
          variants={{
            hidden: {},
            visible: {
              transition: {
                staggerChildren: 0.12,
              },
            },
          }}
        >
          {staffData.map((member, idx) => {
            const [isImageLoaded, setIsImageLoaded] = React.useState(false);
            return (
              <motion.div
                key={member.email}
                initial="hidden"
                whileInView={isImageLoaded ? "visible" : "hidden"}
                viewport={{ once: false, amount: 0.2 }}
                variants={{
                  hidden: { opacity: 0, y: 40 },
                  visible: { opacity: 1, y: 0 },
                }}
                transition={{ duration: 0.6, delay: idx * 0.08 }}
                className="bg-white rounded-2xl shadow-lg p-8 flex flex-col items-center text-center border border-blue-100 hover:shadow-2xl transition-all duration-300"
              >
                <div className="relative mb-4">
                  <img
                    src={member.avatar}
                    alt={member.name}
                    className="w-20 h-20 rounded-full border-4 border-white shadow object-cover bg-white"
                    onLoad={() => setIsImageLoaded(true)}
                    style={{
                      opacity: isImageLoaded ? 1 : 0,
                      transition: "opacity 0.3s",
                    }}
                  />
                  {!isImageLoaded && (
                    <div className="absolute inset-0 w-20 h-20 flex items-center justify-center bg-gray-200 animate-pulse rounded-full" />
                  )}
                </div>
                <div className="font-bold text-lg text-blue-700 mb-1">
                  {member.name}
                </div>
                <div className="text-sm text-gray-500 font-medium mb-2">
                  {member.role}
                </div>
                <div className="text-gray-600 text-sm mb-4">
                  {member.description}
                </div>
                <div className="flex gap-3 mt-auto">
                  <a
                    href={`tel:${member.phone}`}
                    className="inline-flex items-center gap-1 px-3 py-1.5 rounded-full font-semibold text-sm bg-blue-600 text-white hover:bg-blue-700 shadow transition-colors"
                  >
                    <Phone className="w-4 h-4" /> Gọi
                  </a>
                  <a
                    href={`https://zalo.me/${member.zalo}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="inline-flex items-center gap-1 px-3 py-1.5 rounded-full font-semibold text-sm bg-gray-100 text-blue-700 hover:bg-blue-100 shadow transition-colors"
                  >
                    <Zap className="w-4 h-4" /> Zalo
                  </a>
                  <a
                    href={`mailto:${member.email}`}
                    className="inline-flex items-center gap-1 px-3 py-1.5 rounded-full font-semibold text-sm bg-gray-100 text-blue-700 hover:bg-blue-100 shadow transition-colors"
                  >
                    <Mail className="w-4 h-4" /> Email
                  </a>
                </div>
              </motion.div>
            );
          })}
        </motion.div>
      </section>

      {/* Contact form section */}
      <section className="py-16 px-4 bg-white border-t border-blue-100">
        <div className="max-w-3xl mx-auto">
          <div className="bg-white rounded-2xl shadow-xl p-10 border border-blue-100">
            <h2 className="text-2xl font-bold text-blue-700 mb-6 flex items-center gap-2">
              <Mail className="w-6 h-6" /> Gửi tin nhắn đến Kim Tiền
            </h2>
            <div className="mb-4 text-gray-500 text-sm">
              Chúng tôi cam kết bảo mật thông tin khách hàng. Vui lòng để lại
              thông tin của bạn, đội ngũ của chúng tôi sẽ liên hệ với bạn sớm
              nhất có thể!
            </div>
            {formSubmitted ? (
              <div className="text-center py-8">
                <div className="w-12 h-12 bg-green-100 text-green-500 rounded-full flex items-center justify-center mx-auto mb-3">
                  <svg
                    className="w-6 h-6"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth="2"
                      d="M5 13l4 4L19 7"
                    ></path>
                  </svg>
                </div>
                <h3 className="text-lg font-bold text-blue-700 mb-1">
                  Tin nhắn đã gửi!
                </h3>
                <p className="text-gray-600 text-sm">
                  Cảm ơn bạn đã liên hệ với chúng tôi. Chúng tôi sẽ phản hồi sớm
                  nhất có thể.
                </p>
              </div>
            ) : (
              <form onSubmit={handleSubmit}>
                {error && (
                  <div className="mb-3 p-2 bg-red-50 text-red-600 rounded border border-red-200 text-sm">
                    {error}
                  </div>
                )}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-gray-700 text-xs font-medium mb-1">
                      Họ và tên <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="name"
                      value={formData.name}
                      onChange={handleInputChange}
                      className="w-full px-3 py-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-100 focus:border-blue-400 outline-none text-sm"
                      required
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 text-xs font-medium mb-1">
                      Email <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      className="w-full px-3 py-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-100 focus:border-blue-400 outline-none text-sm"
                      required
                    />
                  </div>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                  <div>
                    <label className="block text-gray-700 text-xs font-medium mb-1">
                      Số điện thoại
                    </label>
                    <input
                      type="tel"
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      className="w-full px-3 py-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-100 focus:border-blue-400 outline-none text-sm"
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 text-xs font-medium mb-1">
                      Chủ đề <span className="text-red-500">*</span>
                    </label>
                    <select
                      name="subject"
                      value={formData.subject}
                      onChange={handleInputChange}
                      className="w-full px-3 py-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-100 focus:border-blue-400 outline-none text-sm"
                      required
                    >
                      <option value="">-- Chọn chủ đề --</option>
                      {contactSubjects.map((subject, index) => (
                        <option key={index} value={subject}>
                          {subject}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
                <div className="mb-4">
                  <label className="block text-gray-700 text-xs font-medium mb-1">
                    Tin nhắn <span className="text-red-500">*</span>
                  </label>
                  <textarea
                    name="message"
                    value={formData.message}
                    onChange={handleInputChange}
                    rows={4}
                    className="w-full px-3 py-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-100 focus:border-blue-400 outline-none text-sm"
                    required
                  ></textarea>
                </div>
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className={`w-full px-5 py-2 font-medium rounded bg-blue-600 text-white hover:bg-blue-700 transition-colors text-sm ${isSubmitting ? "opacity-60 cursor-not-allowed" : ""}`}
                >
                  {isSubmitting ? "Đang gửi..." : "Gửi tin nhắn"}
                </button>
              </form>
            )}
          </div>
        </div>
      </section>

      {/* Company Contact Info */}
      <section className="py-20 px-4 bg-white">
        <div className="max-w-6xl mx-auto">
          <div className="text-center mb-16">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">
              Thông tin liên hệ
            </h2>
            <div className="w-24 h-1 bg-blue-600 mx-auto mb-6"></div>
            <p className="text-gray-600 max-w-2xl mx-auto">
              Chúng tôi có nhiều kênh liên hệ để hỗ trợ bạn một cách nhanh chóng và hiệu quả nhất.
            </p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {/* Address */}
            <motion.div
              className="text-center p-6 bg-gray-50 rounded-lg"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6 }}
              viewport={{ once: true }}
            >
              <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
              </div>
              <h3 className="font-semibold text-gray-900 mb-2">Địa chỉ</h3>
              <p className="text-gray-600 text-sm">{companyContact.address}</p>
            </motion.div>

            {/* Phone */}
            <motion.div
              className="text-center p-6 bg-gray-50 rounded-lg"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.1 }}
              viewport={{ once: true }}
            >
              <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Phone className="w-6 h-6 text-green-600" />
              </div>
              <h3 className="font-semibold text-gray-900 mb-2">Điện thoại</h3>
              <a href={`tel:${companyContact.phone}`} className="text-green-600 hover:text-green-700 text-sm font-medium">
                {companyContact.phone}
              </a>
            </motion.div>

            {/* Email */}
            <motion.div
              className="text-center p-6 bg-gray-50 rounded-lg"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.2 }}
              viewport={{ once: true }}
            >
              <div className="w-12 h-12 bg-purple-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <Mail className="w-6 h-6 text-purple-600" />
              </div>
              <h3 className="font-semibold text-gray-900 mb-2">Email</h3>
              <a href={`mailto:${companyContact.email}`} className="text-purple-600 hover:text-purple-700 text-sm font-medium">
                {companyContact.email}
              </a>
            </motion.div>

            {/* Working Hours */}
            <motion.div
              className="text-center p-6 bg-gray-50 rounded-lg"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.3 }}
              viewport={{ once: true }}
            >
              <div className="w-12 h-12 bg-orange-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-6 h-6 text-orange-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
              <h3 className="font-semibold text-gray-900 mb-2">Giờ làm việc</h3>
              <p className="text-gray-600 text-sm">{companyContact.workingHours}</p>
            </motion.div>
          </div>
        </div>
      </section>

      {/* Google Map section */}
      <section className="py-20 px-4 bg-gray-50">
        <div className="max-w-5xl mx-auto rounded-2xl shadow-sm overflow-hidden">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d322.69801405682443!2d106.70177359504456!3d10.865253638687967!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e1!3m2!1svi!2s!4v1752684525268!5m2!1svi!2s"
            width="100%"
            height="340"
            style={{ border: 0 }}
            allowFullScreen
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
            title="Google Maps"
          />
        </div>
      </section>
    </div>
  );
};

export default ContactPage;
