// Helper function to get logo by ID
export const getPartnerLogo = (id: string) => {
  return PARTNER_LOGOS.find(logo => logo.id === id);
};

// Helper function to get all partner logos for display
export const getAllPartnerLogos = () => {
  return PARTNER_LOGOS.map((logo, index) => ({
    src: logo.src,
    alt: logo.alt,
    delay: index * 100, // Animation delay
  }));
};
