import React from 'react';
import { Helmet } from 'react-helmet-async';

interface SEOHeadProps {
  title: string;
  description: string;
  keywords?: string;
  ogTitle?: string;
  ogDescription?: string;
  ogImage?: string;
  ogUrl?: string;
  canonicalUrl?: string;
  structuredData?: object;
  noindex?: boolean;
  nofollow?: boolean;
}

const SEOHead: React.FC<SEOHeadProps> = ({
  title,
  description,
  keywords,
  ogTitle,
  ogDescription,
  ogImage = "https://kimtienposm.com/logo/logo-lg.png",
  ogUrl,
  canonicalUrl,
  structuredData,
  noindex = false,
  nofollow = false
}) => {
  const fullTitle = title.includes('Kim Tiền') ? title : `${title} - Kim Tiền POSM`;
  const robotsContent = `${noindex ? 'noindex' : 'index'},${nofollow ? 'nofollow' : 'follow'}`;

  return (
    <Helmet>
      {/* Basic Meta Tags */}
      <title>{fullTitle}</title>
      <meta name="description" content={description} />
      {keywords && <meta name="keywords" content={keywords} />}
      <meta name="robots" content={robotsContent} />
      
      {/* Canonical URL */}
      {canonicalUrl && <link rel="canonical" href={canonicalUrl} />}
      
      {/* Open Graph Tags */}
      <meta property="og:title" content={ogTitle || fullTitle} />
      <meta property="og:description" content={ogDescription || description} />
      <meta property="og:type" content="website" />
      <meta property="og:image" content={ogImage} />
      <meta property="og:image:width" content="1200" />
      <meta property="og:image:height" content="630" />
      <meta property="og:site_name" content="Kim Tiền POSM" />
      <meta property="og:locale" content="vi_VN" />
      {ogUrl && <meta property="og:url" content={ogUrl} />}
      
      {/* Twitter Card Tags */}
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:title" content={ogTitle || fullTitle} />
      <meta name="twitter:description" content={ogDescription || description} />
      <meta name="twitter:image" content={ogImage} />
      <meta name="twitter:site" content="@kimtienposm" />
      
      {/* Additional Meta Tags */}
      <meta name="author" content="Kim Tiền POSM" />
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
      <meta httpEquiv="Content-Type" content="text/html; charset=utf-8" />
      <meta name="language" content="Vietnamese" />
      <meta name="geo.region" content="VN-SG" />
      <meta name="geo.placename" content="Ho Chi Minh City" />
      <meta name="geo.position" content="10.8231;106.6297" />
      <meta name="ICBM" content="10.8231, 106.6297" />
      
      {/* Business Information */}
      <meta name="business:contact_data:street_address" content="100/1/3 Đường An Phú Đông 25, P. An Phú Đông" />
      <meta name="business:contact_data:locality" content="Quận 12" />
      <meta name="business:contact_data:region" content="TP.HCM" />
      <meta name="business:contact_data:postal_code" content="70000" />
      <meta name="business:contact_data:country_name" content="Vietnam" />
      <meta name="business:contact_data:phone_number" content="+84919535525" />
      <meta name="business:contact_data:email" content="kimtien@kimtienposm.com" />
      
      {/* Structured Data */}
      {structuredData && (
        <script type="application/ld+json">
          {JSON.stringify(structuredData)}
        </script>
      )}
      
      {/* Default Organization Schema */}
      <script type="application/ld+json">
        {JSON.stringify({
          "@context": "https://schema.org",
          "@type": "Organization",
          "name": "Kim Tiền POSM",
          "alternateName": "Kim Tiền",
          "url": "https://kimtienposm.com",
          "logo": "https://kimtienposm.com/logo/logo-lg.png",
          "contactPoint": {
            "@type": "ContactPoint",
            "telephone": "+84919535525",
            "contactType": "customer service",
            "areaServed": "VN",
            "availableLanguage": "Vietnamese"
          },
          "address": {
            "@type": "PostalAddress",
            "streetAddress": "100/1/3 Đường An Phú Đông 25, P. An Phú Đông",
            "addressLocality": "Quận 12",
            "addressRegion": "TP.HCM",
            "postalCode": "70000",
            "addressCountry": "VN"
          },
          "sameAs": [
            "https://www.facebook.com/kimtienposm",
            "https://www.instagram.com/kimtienposm",
            "https://www.linkedin.com/company/kimtienposm"
          ],
          "description": "Công ty chuyên cung cấp dịch vụ in ấn, thiết kế và sản xuất POSM chất lượng cao tại TP.HCM",
          "foundingDate": "2020",
          "numberOfEmployees": "10-50",
          "industry": "Printing and Publishing",
          "serviceArea": {
            "@type": "GeoCircle",
            "geoMidpoint": {
              "@type": "GeoCoordinates",
              "latitude": 10.8231,
              "longitude": 106.6297
            },
            "geoRadius": "50000"
          }
        })}
      </script>
    </Helmet>
  );
};

export default SEOHead;
